import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

import { SharedService } from '../../shared/services/shared.service';
import { CookieService } from 'ngx-cookie';

@Component({
  selector: 'app-login-signup',
  templateUrl: './login-signup.component.html',
  styleUrls: ['./login-signup.component.css']
})
export class LoginSignupComponent implements OnInit {

    public login = {
      username : '',
      password: ''
    }
    public register = {
        // firstName:'',
        // lastName:'',
        username1:'',
        email:'',
        password:'',
        username:'',
        domain: "web",
        roles:'U'
    }
    public userType        = 'endUser';
    public acceptTerms     = false;
    public errLogin        = false;
    public errRegister     = '';
    public successRegister = '';
    // email@e.coml
    constructor(
        private _router : Router,
        private _sharedService: SharedService,
        private _cookieService: CookieService ) { }

    ngOnInit() { }

    loginUser() {
        this.errLogin = false;
        let data = JSON.parse(JSON.stringify(this.login));
        this._sharedService.loader('show');
        this._sharedService.login(data).then((res: any) => {
             this._sharedService.loader('hide');
            if(res.success) {
             this._sharedService.showAlert('Login Successfully','alert-success')
                let route = '/item-dashboard';
                this._cookieService.put('token', res.data.access_token);
                this._cookieService.put('roles', res.data.roles);
                this._cookieService.put('loginID', res.data.id);
                this._cookieService.putObject('user', res.data );
                if(res.data.roles == 'U') route = '/dashboard';
                this._router.navigate([route]);

            } else {
                this._sharedService.showAlert(res.error.message,'alert-success')
                this.errLogin = true;
            }

        },(error) => {
            this._sharedService.showAlert("There are some error please try after some time.",'alert-success')        
        });
    }

    registerUser() {
        if(!this.acceptTerms) {
            this._sharedService.showAlert('Please accept terms and conditions.','alert-danger');
            // this.errRegister = 'Please accept terms and conditions.'
            return;
        }
        this.errRegister = ''
        let data = JSON.parse(JSON.stringify(this.register));
        data['username'] = data['email'];
        data['Type'] = data['roles'];
         this._sharedService.loader('show');
         this._sharedService.register(data).then((res: any) => {
             this._sharedService.loader('hide');
             if(res.success) {
                 this._sharedService.showAlert('Registered Successfuly','alert-success');
                // this.successRegister = 'Register Successfuly';
                this.register = {
                    // firstName:'',
                    // lastName:'',
                    username1:'',
                    username:'',
                    email:'',
                    password:'',
                    domain : "web",
                    roles:'U'
                }
            } else {
                this._sharedService.showAlert(res.error.message,'alert-danger');
                // this.errRegister = res.error.message;
            }
        },(error) => {
            this._cookieService.put('token', 'token' );
            this._cookieService.putObject('actions', {'actions':'A'} );        
        });   
    }

    changeType() {
        this._sharedService.loader('show');
        setTimeout(()=>{
            this._sharedService.loader('hide');
            window.scrollTo(500, 0);
            this.register.roles = 'D';
            this.userType = 'dispensary';
        },500);
    }

}



